function getClinics() {
  return Object.keys(clinicData)
  // [
  //   "53CF3389-6555-43D2-9D17-9890841F5BCA",
  //   "085e44a4-0a52-45c7-bee6-28ce61a3c42a"
  // ];
}

function dowOf(day) {
  return [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday"
  ].indexOf(day);
}

class Schedule extends React.Component {
  constructor(props) {
    super(props);
    this.schedule = [];
    let slots, start;
    for (var key of [0, 1, 2, 3, 4, 5, 6]) {
      slots = [];
      start = dayjs()
        .set("hour", 6)
        .set("minute", 0)
        .set("second", 0)
        .set("millisecond", 0);
      while (start.hour() < 19) {
        slots.push({
          start: start,
          end: start.add(15, "minute"),
          selected: false
        });
        start = start.add(15, "minute");
      }
      this.schedule[key] = slots;
    }
    this.schedule[0] = [];
  }

  export() {
    // console.log("exportng");
    let clinics = getClinics();
    let ret = {};
    clinics.forEach(clinic => {
      ret[clinic] = {};
    });
    for (var i = 0; i < 7; i++) {
      this[`day_${i}`].export();
    }
    // console.log(ret);
    return ret;
  }

  render() {
    let s = schedule[this.props.doctorId];
    s.clinics = s.clinics || {}
    let keys = Object.keys(s.clinics);
    if (!this.props.show) return "";
    return (
      <table cellPadding="10">
      <tbody>
        <tr>
          <td>
            <HourPicker
              key="0"
              day="Sunday"
              doctor={this.props.doctorId}
              ref={ref => {
                this.day_0 = ref;
              }}
              clinic={keys.find(key => s.clinics[key][0])}
            />
          </td>
          <td>
            <HourPicker
            key="1"
              day="Monday"
              ref={ref => {
                this.day_1 = ref;
              }}
              doctor={this.props.doctorId}
              clinic={keys.find(key => s.clinics[key][1])}
            />
          </td>
          <td>
            <HourPicker
            key="2"
              day="Tuesday"
              ref={ref => {
                this.day_2 = ref;
              }}
              doctor={this.props.doctorId}
              clinic={keys.find(key => s.clinics[key][2])}
            />
          </td>
          <td>
            <HourPicker
            key="3"
              day="Wednesday"
              ref={ref => {
                this.day_3 = ref;
              }}
              doctor={this.props.doctorId}
              clinic={keys.find(key => s.clinics[key][3])}
            />
          </td>
          <td>
            <HourPicker
            key="4"
              day="Thursday"
              ref={ref => {
                this.day_4 = ref;
              }}
              doctor={this.props.doctorId}
              clinic={keys.find(key => s.clinics[key][4])}
            />
          </td>
          <td>
            <HourPicker
            key="5"
              day="Friday"
              ref={ref => {
                this.day_5 = ref;
              }}
              clinic={keys.find(key => s.clinics[key][5])}
            />
          </td>
          <td>
            <HourPicker
            key="6"
              day="Saturday"
              ref={ref => {
                this.day_6 = ref;
              }}
              doctor={this.props.doctorId}
              clinic={keys.find(key => s.clinics[key][6])}
            />
          </td>
        </tr>
        </tbody>
      </table>
    );
  }
}

const overlap = (appointment, slot) => {
  // console.log({appointment: appointment})
  // console.log({slot: slot})
  return appointment.start.toJSON() === slot.start.toJSON();
  // return appointment.start < slot.end && appointment.end > slot.start;
};

class HourPicker extends React.Component {
  constructor(props) {
    super(props);
    let i,
      slot,
      slots = [];
    let start = dayjs()
      .set("hour", 6)
      .set("minute", 0)
      .set("second", 0)
      .set("millisecond", 0);
    while (start.hour() < 19) {
      slots.push({
        start: start,
        end: start.add(15, "minute"),
        selected: false
      });
      start = start.add(15, "minute");
    }

    let dow = dowOf(this.props.day);

    let arr, hour, minute;
    let appointment;

    try {
      let data,
        doctor = schedule[this.props.doctor];
      if (doctor) {
        let clinic = doctor.clinics[this.props.clinic];
        if (clinic) {
          if ((data = clinic[dow])) {
            // // console.log(clinic);
            if (data.hours) {
              // // console.log({ hours: data.hours });
              data.hours.forEach(block => {
                appointment = {};
                if (!block.start) return;

                arr = block.start.split(":").map(i => parseInt(i));
                hour = arr[0];
                minute = arr[1];

                appointment.start = dayjs()
                  .set("hour", hour)
                  .set("minute", minute)
                  .set("second", 0)
                  .set("millisecond", 0);
                arr = block.end.split(":").map(i => parseInt(i));
                hour = arr[0];
                minute = arr[1];

                appointment.end = dayjs()
                  .set("hour", hour)
                  .set("minute", minute)
                  .set("second", 0)
                  .set("millisecond", 0);
                // // console.log(block);
                for (i = 0; i < slots.length; i++) {
                  // // console.log(slots[i], block);
                  if (overlap(slots[i], appointment)) {
                    // console.log(slots[i], appointment);
                    // console.log('YES!');
                    //
                    slots[i].selected = true;
                  } else {
                    // console.log('NO!');
                    // console.log(slots[i].start.toJSON() );
                    // console.log(appointment.start.toJSON() );
                    // console.log(slots[i].start == appointment.start );


                  }
                }
              });
            }
          }
        }
      }
    } catch (e) {
      // console.log(e);
      // console.log(e.stack);
    }
    this.state = { slots, clinic: this.props.clinic };
  }

  export() {
    // // console.log("-- exporting");
    return { clinic: this.state.clinic, slots: this.state.slots };
  }

  setClinic(props, clinic, s) {
    this.setState({ clinic: clinic });
    // setTimeout(() => {
    //   // console.log(this.state.clinic);
    // }, 100);

    // let dow = dowOf(props.day);
    // // console.log(dow, clinic, props.doctor);
    // // console.log({ s: s });
    // let clinics = getClinics();
    // if (clinic === "") {
    //   // console.log("deleting");
    //   delete schedule[props.doctor]["clinics"][clinic][dow];
    //   // console.log(schedule[props.doctor]["clinics"][clinic]);
    // } else {
    //   if (!schedule[props.doctor]["clinics"][clinic]) {
    //     schedule[props.doctor]["clinics"][clinic] = "";
    //   }
    // }
    // this.setState(schedule);
  }

  toggleSelected(s, props) {
    let index = this.state.slots.findIndex(slot => slot.start === s.start);
    this.state.slots[index] = {
      start: s.start,
      end: s.end,
      selected: !s.selected
    };

    this.setState({ slots: this.state.slots });
    let dow = dowOf(props.day);
    // let clinic = this[`textInput${props.day}${props.doctor}`];
    // console.log(s, props);
  }

  render() {
    let d = this.props.clinic ? this.props.clinic : "";
    // // console.log(d);
    let slots = this.state.slots.map(s => {
      let key = dowOf(this.props.day) + ' ' + s.start.format("HH:mm")
      // console.log(key)
      return <div
        style={{
          whiteSpace: "nowrap",
          padding: "3px 25px 3px 25px",
          border: "solid 1px black",
          cursor: "pointer",
          userSelect: "none",
          backgroundColor: s.selected ? "yellow" : "#dddd"
        }}
        key={key}
        className={s.selected ? "slot selected" : ""}
        id={key}
        onClick={this.toggleSelected.bind(this, s, this.props)}
      >
        {s.start.format("HH:mm")} - {s.end.format("HH:mm")}
      </div>
    })

    let options = getClinics().map(id => {
      return <Option value={id} key={id}>
        {clinicData[id].name}
      </Option>
    })

    return (
      <div>
        <h3 align="center">{this.props.day}</h3>
        <Select

          defaultValue={d}
          style={{ width: 150 }}
          onChange={this.setClinic.bind(this, this.props)}
        >
          <OptGroup label="Clinic">
            <Option value="">---</Option>
            {options}
          </OptGroup>
        </Select>
        <hr />

        {slots}
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      step: "one",
      doctor: {
        title: "Doctor 3"
      }
    };
  }

  save() {
    // alert("Saved!");
    // console.log(this["schedule_2cc1047b-f5fd-4534-bb11-8f9f1d07849d"]);
    // this["schedule_2cc1047b-f5fd-4534-bb11-8f9f1d07849d"].export();
    // ["---", "King of Prussia", "King of Prussia", "King of Prussia", "---", "King of Prussia", "---"]
    let newSchedule = {}
    let clinic, clinics = $('.ant-select-selection-selected-value').get().map(s => $(s).attr('title'))
    let slots

    for(var i in clinics){
      clinic = clinics[i]
      if(!clinic.match(/\w/)) continue
      if(!newSchedule[clinic]) newSchedule[clinic] = {}
      // console.log(clinic)
      // slots = $('.slot.selected[id^="' + i + ' "]')
      slots = $('.slot.selected[id^="' + i + ' "]').get().map(s => {
        let arr = $(s).text().split(' - ')
        return {
          start: arr[0],
          end: arr[1],
        }

      })
      newSchedule[clinic]['' + i] = {hours: slots}
    }
    let id = $('.ant-list-item-meta-description').text()
    // console.log(newSchedule)
    schedule[id].clinics = newSchedule
    try{
      saveSchedule()
    } catch(e){

    }
    // console.log(JSON.stringify(schedule))
  }

  setStep(step) {
    this.setState({ step });
  }

  setDoctor(item) {
    this.setState({ doctor: item });
    this.setStep("two");
  }

  onToggleDoctor(doctor, checked) {
    // console.log(`switch to ${checked}`);
    schedule[doctor.id].disabled = !checked;
    // console.log(doctor);
    this.setState({ schedule });
    saveSchedule()
  }

  showModal = () => this.setState({ visible: true });
  showClinicsModal = () => this.setState({ clinicsVisible: true });

  addDoctor(name, id) {
    try {
      schedule[id] = {
        nickname: name,
        clinics: {}
      };
      [
        "085e44a4-0a52-45c7-bee6-28ce61a3c42a",
        "53CF3389-6555-43D2-9D17-9890841F5BCA"
      ].forEach(clinic => {
        schedule[id]["clinics"][clinic] = {};
      });
      // console.log(id, schedule[id]);

      this.setState({ doctor: id });
      saveSchedule()
    } catch (e) {
      console.log(e.stack);
    }
  }

  deleteDoctor(id, name) {
    if (confirm(`Delete ${name} ?`)) {
      delete schedule[id];
      this.setState({ doctor: null, step: "one" });
      saveSchedule()
    }
  }

  handleOk = e => {
    this.addDoctor(
      this.doctorNameInput.input.value,
      this.doctorIdInput.input.value
    );
    this.setState({
      visible: false
    });
  };

  handleEditClinicsOk = e => {
    // console.log('clinics ok')
    let items = $('[id^=clinic_]').get().map(el => ({id: $(el).attr('id').replace('clinic_', ''), name: $(el).attr('value')}))
    let oldName, newName, tmpClinic
    for(var item of items){
      oldName = clinicData[item.id].name
      newName = item.name
      clinicData[item.id].name = newName
      Object.keys(schedule).map(pid => {
        if(schedule[pid].clinics && schedule[pid].clinics[oldName]){
          tmpClinic = schedule[pid].clinics[oldName]
          delete schedule[pid].clinics[oldName]
          schedule[pid].clinics[newName] = tmpClinic

        }

      })
    }
    schedule['clinicData'] = clinicData
    try{
      saveSchedule()
    } catch(e){
      console.log(e)
    }

    this.setState({
      clinicsVisible: false
    });
  };

  handleCancel = e => {
    // console.log(e);
    this.setState({
      visible: false, clinicsVisible: false
    });
  };

  getAvatar(info) {
    // console.log("--avatar");
    // console.log(info);
    try{
      let file = info.fileList[info.fileList.length - 1].originFileObj;

      const reader = new FileReader();
      let that = this;

      reader.onload = () => {
        const dataURL = reader.result;

        that.setAvatar(dataURL);
      };

      reader.readAsDataURL(file);
    } catch(e){

    }
  }

  setAvatar(url) {
    // // console.log(url);
    let id = this.state.doctor.id;
    // // console.log(id);

    // // console.log(schedule[id]);
    schedule[id].avatar = url;
    this.setState({ avatar: url });
    // console.log(url.length);
    // // console.log({ avatar: schedule[id].avatar });
  }



  render() {
    let pid
    for(pid of Object.keys(schedule)){
      if(schedule[pid].disabled === 'false'){
        schedule[pid].disabled = false
      }
      console.log(pid)
      console.log(schedule[pid].disabled)
    }

    let doctorIds = Object.keys(schedule).filter(id => id !== 'clinicData');
    let doctors = doctorIds.map(id => {
      let provider = schedule[id];
      return {
        id: id,
        disabled: provider.disabled,
        title: provider.nickname,
        clinics: provider.clinics
      };
    });

    if (this.state.step !== "one") {
      doctors = doctors.filter(d => d.id === this.state.doctor.id);
    }

    //   ? this.doctors
    //   :

    let buttons =
      this.state.step === "one" ? (
        <div>
          <Button type="primary" onClick={this.showModal}>
            Add Doctor
          </Button>
          <Modal
            title="Add Doctor"
            visible={this.state.visible}
            onOk={this.handleOk.bind(this)}
            onCancel={this.handleCancel.bind(this)}
          >
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              ref={input => {
                this.doctorNameInput = input;
              }}
              placeholder="Doctor Name"
            />

            <Input
              prefix={<Icon type="id" style={{ color: "rgba(0,0,0,.25)" }} />}
              type="id"
              ref={input => {
                this.doctorIdInput = input;
              }}
              style={{ marginTop: "10px" }}
              placeholder="Doctor ID"
            />
          </Modal>
          <Button type="primary" onClick={this.showClinicsModal} style={{ marginLeft: "10px" }}>
            Edit Clinics
          </Button>
          <Modal
            title="Edit Clinics"
            visible={this.state.clinicsVisible}
            onOk={this.handleEditClinicsOk.bind(this)}
            onCancel={this.handleCancel.bind(this)}
          >

          {Object.keys(clinicData).map(id => {
            return <div key={'div_' + id}>
              <p>{id}</p>
              <Input
                className="clinic"
                id={'clinic_' + id}
                style={{marginBottom:"10px"}}
                prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
                placeholder="Clinic Name"
                defaultValue={clinicData[id].name}
              />
            </div>
          })}




          </Modal>
        </div>
      ) : (
        <table>
          <tbody>
          <tr>
            <td>
              <Button type="primary" onClick={this.setStep.bind(this, "one")}>
                <Icon type="left" />
                Go back
              </Button>
            </td>
            <td>
              <Upload
                name="file"
                // action="//jsonplaceholder.typicode.com/posts/"
                style={{ display: "inline" }}
                showUploadList="false"
                onChange={this.getAvatar.bind(this)}
              >
                <Button
                  type="primary"
                  //  style={{ marginLeft: "20px" }}
                >
                  <Icon type="upload" /> Change Photo
                </Button>
              </Upload>
            </td>
            <td>
              <Button
                type="primary"
                onClick={this.save.bind(this)}
                // style={{ marginLeft: "20px" }}
              >
                Save
                <Icon type="save" />
              </Button>
            </td>
          </tr>
          </tbody>
        </table>
      );
    let content = (
      <div>
        {buttons}
        <List
          itemLayout="horizontal"
          dataSource={doctors}
          renderItem={item => (
            <div>
              <List.Item>
                <Icon
                  type="delete"

                  style={this.state.step === "one" ? { margin: "0px 0px 10px" } : { display: "none" }}
                  onClick={this.deleteDoctor.bind(this, item.id, item.title)}
                />
                <Switch
                  size="small"
                  style={this.state.step === "one" ? { margin: "0px 10px 12px" } : { display: "none" }}
                  defaultChecked={!schedule[item.id].disabled}

                  onChange={this.onToggleDoctor.bind(this, item)}
                />
                <List.Item.Meta
                  avatar={<Avatar src={schedule[item.id].avatar} />}
                  title={item.title}
                  description={item.id}
                  onClick={
                    schedule[item.id].disabled
                      ? null
                      : this.setDoctor.bind(this, item)
                  }
                />
              </List.Item>
              <Schedule
                doctorId={item.id}
                show={this.state.step === "two"}
                ref={ref => {
                  // console.log(`schedule_${item.id}`);
                  this[`schedule_${item.id}`] = ref;
                }}
              />
            </div>
          )}
        />
      </div>
    );

    return (
      <div className="App">
        {content}
      </div>
    );
  }
}
export default App;
ReactDOM.render(<App />, document.getElementById("root"));
